var ctime = parseInt(Date.now());
const total = 10;
let offset = 0;
export default {

	data() {
		return {
			loadingType: 0,
			loadingText: {
				contentdown: "",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			list: [],
		};
	},
	onLoad() {
		this.init()
	},
	onUnload() {
		offset = 0;
	},
	methods: {
		init() {
			this.api.center.shield.get_list({
				ctime,
				offset,
				total
			}, res => {
				console.log(res.data)
				if (res.data.length) {
					this.list = this.list.concat(res.data)
					this.loadingType = 0
				} else {
					this.loadingType = 2
				}
			})
		},
		loadMore() {
			if (this.loadingType != 0) {
				return
			}
			this.loadingType = 1
			offset += total
			this.init()
		},
		cancel(id,index){
			this.api.home.article.toggle_shield({
				vid:id,
				action:0
			},res=>{
				this.list.splice(index,1)
				uni.showToast({
					title:"操作成功"
				})
			})
		}
	},
	onReachBottom() {
		this.loadMore()
	}
}
